a,
a:hover {
  text-decoration: none;
}
.item {
  margin: 0px auto;
  border:solid thin;
  box-shadow: 12px 18px 5px 0px rgba(0, 0, 0, 0.42);
  -webkit-box-shadow: 12px 18px 5px 0px rgba(0, 0, 0, 0.42);
  -moz-box-shadow: 12px 18px 5px 0px rgba(0, 0, 0, 0.42);
}
h2,
h3,
h4,
h5 {
  margin: 0px;
}

/* ---- UI Style ----- */
.item {
  background-color: #fff;
  width: 340px;
  font-family: var(--ff-love);
}
.item .image {
  overflow: hidden;
}
.item .image img {
  width: 100%;
}

.item .text {
  padding: 2em;
  text-align: center;
}
.item .text figcaption {
  position: relative;
  z-index: 1;
}
.item .text a {
  color: rgba(0, 0, 0, 0.7);
}
.item .text p {
  color: rgba(0, 0, 0, 0.863);
  line-height: 22px;
  font-weight: 300;
  font-size: 14px;
}
.item .text .fa {
  font-size: 18px;
  width: 15px;
  height: 15px;
  padding: 13px;
  border: solid 1px rgba(0, 0, 0, 0.8);
  border-radius: 50%;
}

/* ---- Responsive Code ---- */
@media (max-width: 768px) {
  .item {
    width: 100%;
  }
}

